.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  /* width: 100vw; */
  /* height: 100vh; */
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  /* padding: 1rem; */
  width: 100%;
  height: 100%;
  border-collapse: collapse;
  font-size: 0.75rem; /* Smaller font for mobile */
  table-layout: fixed;
}

.modal-box {
  background-color: #fff;
  width: 100%;
  max-width: 500px;
  max-height: 90vh;
  overflow-y: auto;
  border-radius: 12px;
  padding: 1.5rem 1rem;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.25);
  position: relative;
  animation: fadeIn 0.3s ease-out;
  overflow-x: auto;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.modal-header h2 {
  flex: 1;
  text-align: center;
  font-size: 1.2rem;
  margin: 0;
}

.close-button {
  font-size: 1.5rem;
  border: none;
  background: none;
  position: absolute;
  right: 1rem;
  top: 1rem;
  cursor: pointer;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* padding: 1rem; */
}

/* .modal-image {
  width: 100%;
  height: auto;
  border-radius: 8px;
} */

@keyframes fadeIn {
  from { transform: translateY(20px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

.legend {
  /* margin: 20px 0; */
  font-family: Arial, sans-serif;
}

.legend h3 {
  margin-bottom: 10px;
}

.legend ul {
  list-style: none;
  padding: 0;
  display: flex;
  gap: 5px;
  flex-wrap: wrap;
  margin: 0;
}

.legend li {
  display: flex;
  align-items: center;
  font-size: 0.9em;
}

.legend-box {
  display: inline-block;
  width: 24px;
  height: 24px;
  margin-right: 8px;
  text-align: center;
  line-height: 24px;
  border: 1px solid #444;
  font-weight: bold;
  font-family: monospace;
}

