table {
  border-collapse: collapse;
  margin: 20px 0;
  width: 100%;
  height: 100%;
  /* max-width: 100%; */
  font-weight: bold;
  /* overflow-x: auto; */
  /* display: block; */
}

/* thead th {
  position: sticky;
  top: 0;
  background-color: #f0f0f0;
  z-index: 1;
} */

caption {
  font-size: 1.3em;
  font-weight: bold;
  margin-bottom: 8px;
}

th, td {
  border: 1px solid #444;
  padding: 4px;
  text-align: center;
  word-wrap: break-word;
}

th {
  background-color: #f0f0f0;
}

/* td {
} */

/* Color coding */
.H { background-color: #f88; color: black; }  /* Hit - Red */
.S { background-color: #ff8; color: black; }  /* Stand - Yellow */
.D { background-color: #8f8; color: black; }  /* Double - Green */
.P { background-color: #88f; color: black; }  /* Split - Blue */

.action-H {
  color: #f88; 
}
.action-S {
  color: #ff8; 
}
.action-D {
  color: #8f8; 
}
.action-P {
  color: #88f;
}

.book-navigation {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1rem;
}

.nav-button {
  background-color: #eee;
  border: none;
  padding: 0.5rem 1rem;
  font-size: 1.2rem;
  margin: 0 1rem;
  cursor: pointer;
  border-radius: 4px;
  /* transition: background 0.2s ease; */
}

.nav-button:hover {
  background-color: #ddd;
}

/* For very small devices */
@media screen and (max-width: 600px) {
  .modal-box {
    /* width: 95%; */
    max-height: 90vh;
    overflow-y: auto;
    font-size: 0.6rem;
  }

  .modal-header h2 {
    font-size: 1rem;
  }

  .close-button {
    font-size: 1.2rem;
  }
  
  .legend {
    font-size: 0.85em;
  }

  .legend-box {
    width: 20px;
    height: 20px;
    line-height: 20px;
    font-size: 0.85em;
  }

  caption {
    font-size: 1.1em;
  }

  th, td {
    padding: 6px;
    font-size: 0.85em;
  }
}